@charset "UTF-8";

@font-face {
  font-family: "untitled-font-5";
  src:url("fonticons/untitled-font-5.eot");
  src:url("fonticons/untitled-font-5.eot?#iefix") format("embedded-opentype"),
    url("fonticons/untitled-font-5.woff") format("woff"),
    url("fonticons/untitled-font-5.ttf") format("truetype"),
    url("fonticons/untitled-font-5.svg#untitled-font-5") format("svg");
  font-weight: normal;
  font-style: normal;

}

[data-icon]:before {
  font-family: "untitled-font-5" !important;
  content: attr(data-icon);
  font-style: normal !important;
  font-weight: normal !important;
  font-variant: normal !important;
  text-transform: none !important;
  speak: none;
  line-height: 1;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

[class^="icon-"]:before,
[class*=" icon-"]:before {
  font-family: "untitled-font-5" !important;
  font-style: normal !important;
  font-weight: normal !important;
  font-variant: normal !important;
  text-transform: none !important;
  speak: none;
  line-height: 1;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

